import { Controller } from '@ali/4ever-cangjie';
import { updateHighlightSingle } from '../utils';
import type { IPayload } from '../actions';

export default function updateHighlight (controller: Controller, payload: IPayload, isHighlightBorder: boolean) {
  if (isHighlightBorder) {
    const { node, type, reason } = payload;
    const nodes = Array.isArray(node) ? node : [node];
    nodes.forEach((node) => {
      updateHighlightSingle(node, type, reason);
    });
  } 
}